
@import url('https://fonts.googleapis.com/css2?family=Kenia&family=League+Spartan:wght@100&family=Lobster&family=Montserrat&family=Orbitron&family=Poppins:wght@100;200;300&family=Roboto&family=Rubik&family=Rubik+Mono+One&family=Russo+One&family=Staatliches&family=VT323&family=Vina+Sans&display=swap');
*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    
}


::-webkit-scrollbar{
    width: 0;
}

:root{
    --logo:black;
    --navlink:black;
    --navlinkh:grey;
}

h1{
    font-size: 4rem;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
    
}
h4{
    font-family:Lato ;
}
.center{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
h6{
    font-weight: 700;
}

h5{
    font-size: 2rem;
    font-weight: 600;
}

h2{
    font-size: 3rem;
    font-weight: 800;

}

.main img{
    width:300px;
}

.navbar-brand{
    font-size: 2rem;
    color: var(--logo);
    font-family: 'Lobster', cursive;
}
.navbar-brand:hover{
    color: var(--logo);
}
.nav-link{
    margin-right: 10px;
    margin-left: 10px;
    color: var(--navlink);
    font-family: 'Rubik', sans-serif;
    font-size: 19px;
    text-transform: uppercase;
}
.nav-link:hover{
    color: var(--navlinkh);
    
}

.main{
    background-color: #8BC6EC;
    background-image: linear-gradient( 105.3deg,  rgba(30,39,107,1) 21.8%, rgba(77,118,221,1) 100.2% );

}
.main h1{
    font-size: 5rem;
    font-weight: 570;
    font-family: 'Rubik Mono One', sans-serif;
    color: white;
   
}
h1{
    font-size: 4rem;
    font-weight: 600;
    
}

.btn1{
   padding: 10px;
    margin-top: 7px;
    border: none;
    outline: none;
    background-color: #f0425f;
    color: white;
    font-weight: 700;
    border-radius: 60px ;
    text-decoration: none;
    font-size: 25px;
}
.btn1:hover{
    background-color:#ec8092;
    color: white;
}

.btn2{
    background-image: linear-gradient(135deg, #8BC6EC 0%, #b67fe7 100%);
    border-radius: 60px;
    height: 55px;
    width: 70%;
    color: white;
    font-size: 18px;
    font-weight: 900;
    border: none;
}
.btn2:hover{
 background: white;
 border: 2px solid  rgba(30,39,107,1) ;
 scale: 10vh;
}

 a:hover{
    color:  rgba(30,39,107,1) ;
