.study{
    background-image: linear-gradient( 105.3deg,  rgba(30,39,107,1) 21.8%, rgba(77,118,221,1) 100.2% );
}

.study h1{
    font-weight: 800;
    color: white;
}

.btn3{
    height: 45px;
    width: 35%;
    border: none;
    outline: none;
    background-color:#f0425f;
    color: white;
    font-weight: 700;
    border-radius: 60px ;
}
.btn3:hover{
    background: white;
    border: 2px solid #f0425f;
    color:#f0425f;
    scale: 10vh;
}

.newsletter{
    background-color: black;
    color: white;
}

.newsletter input{
    height: 45px;
    width: 55%;
    border: 2px solid white;
    outline: none;
    color: white;
    background-color: transparent;
    margin-top: 20px;
    font-weight: 600;
}
.btn{
    height: 44.5px;
    width: 100px;
    background-color: white;
    margin-left: -10px;
    margin-bottom: 5px;
    font-weight: 600;
    border-radius: 0;
    color: black;
}
.btnnews{
    height: 44.9px;
    width: 100px;
    background-color: white;
    border: 1px solid white;
    margin-left: -10px;
    margin-bottom: 5px;
    font-weight: 600;
    border-radius: 0;
    color: black;
}
.footerlogo{
    margin-bottom: 10px;
    font-size: 2rem;
    color: white;
    font-family: 'Lobster', cursive;
}

.newsletter a{
    font-size: 1rem;
